<template>
  <div class="page page-roadshow-list">
    <div class="list-options">
      <!--  :labelCol="{ span: 10 }" :wrapperCol="{ span: 10 }"  -->
      <div class="flex items-center">
        <div class="flex items-center">
          <span>状态：</span>
          <a-select allowClear size="small" v-model="params.status" @change="onStatusChange" placeholder="选择状态" style="width: 100px">
            <a-select-option :value="-1">全部</a-select-option>
            <a-select-option :value="0">正常</a-select-option>
            <a-select-option :value="1">已冻结</a-select-option>
          </a-select>
        </div>
        <div class="flex items-center" style="margin-left: 50px">
          <span>关键字：</span>
          <a-input size="small" v-model="params.keyword" style="width: 200px" placeholder="手机/用户名/邮箱" />
        </div>
        <a-button style="margin-left: 50px" type="primary" icon="search" size="small" :loading="loading" @click="handleSearch">查询</a-button>
      </div>
    </div>
    <a-divider></a-divider>
    <a-space class="v-mb-md">
      <a-button type="primary" size="small" style="min-width: 60px" @click="handleAccount(1)">冻结</a-button>
      <a-button type="primary" size="small" style="min-width: 60px" @click="handleAccount(2)">解冻</a-button>
    </a-space>
    <div class="list-container">
      <a-table
        :columns="columns"
        :dataSource="list"
        :pagination="{
          showQuickJumper: true,
          showSizeChanger: true,
          hideOnSinglePage: false,
          pageSizeOptions: ['10', '20', '30', '50'],
          total: pagination.totalRow,
          pageSize: pagination.pageSize,
          current: pagination.pageNumber,
          onChange: onPageChange,
          onShowSizeChange: onPageChange,
        }"
        :rowSelection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }"
        :scroll="{ x: '100%' }"
        :loading="loading"
        tableLayout="auto"
        class="text-no-wrap"
        rowKey="id"
        size="small"
      >
        <template slot="action" slot-scope="text, record">
          <a-popconfirm @confirm="updateDoc(record)" cancelText="取消" okText="确认" placement="top">
            <template slot="title">
              <div v-html="record.status === 0 ? '确认冻结 ?' : '确认解冻 ?'"></div>
            </template>
            <a-icon slot="icon" style="color: red" type="info-circle" />
            <a-button class="text-indigo" :icon="record.status === 0 ? 'eye' : 'eye-invisible'" size="small" :title="record.status === 0 ? '发布' : '取消发布'" type="link"></a-button>
          </a-popconfirm>
          <!-- <a-button class="text-red" icon="delete" size="small" title="删除" type="link"></a-button> -->
          <a-popconfirm placement="top" okText="确认" cancelText="取消" @confirm="onDelete(record)">
            <template slot="title">
              <div>确认重置 ?</div>
            </template>
            <a-icon slot="icon" type="info-circle" style="color: red" />
            <a-button class="text-red" icon="lock" size="small" title="重置密码" type="link"></a-button>
          </a-popconfirm>
        </template>
        <template slot="status" slot-scope="text">
          <span :class="{ 'text-grey': text === 1 }">{{ text === 0 ? '正常' : '已冻结' }}</span>
        </template>
        <template slot="sex" slot-scope="text">
          <span>{{ text === 0 ? '男' : '女' }}</span>
        </template>
      </a-table>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      loading: false,
      list: [],
      selectedRowKeys: [],
      selectedRows: [],
      pagination: {
        totalPage: 1,
        pageNumber: 1,
        pageSize: 10,
        totalRow: 0,
      },
      params: {
        keyword: '',
        status: undefined,
      },
      columns: [
        {
          title: '序号',
          dataIndex: 'id',
          width: 50,
          align: 'center',
          customRender: (text, record, index) => index + 1,
        },
        {
          title: '头像',
          dataIndex: 'avator',
          align: 'center',
          width: 80,
          scopedSlots: { customRender: 'avator' },
        },
        {
          title: '姓名',
          dataIndex: 'realname',
        },
        {
          title: '手机',
          dataIndex: 'phone',
          // width: 100,
        },
        {
          title: '邮箱',
          dataIndex: 'email',
          // width: 120,
        },
        {
          title: '微信号',
          dataIndex: 'wx',
          width: 150,
        },
        {
          title: '状态',
          dataIndex: 'status',
          width: 60,
          align: 'center',
          scopedSlots: { customRender: 'status' },
        },
        {
          title: '昵称',
          dataIndex: 'nickname',
          // width: 120,
        },
        {
          title: '性别',
          dataIndex: 'sex',
          width: 40,
          align: 'center',
          scopedSlots: { customRender: 'sex' },
        },
        {
          title: '年龄',
          dataIndex: 'age',
          width: 50,
          align: 'center',
        },

        {
          title: '最近登录时间',
          dataIndex: 'loginTime',
          align: 'center',
          width: 122,
        },
        {
          title: '操作',
          dataIndex: 'action',
          width: 100,
          fixed: 'right',
          align: 'center',
          scopedSlots: { customRender: 'action' },
        },
      ],
    }
  },
  computed: {
    user: (vm) => vm.$store.state.user.user,
  },
  methods: {
    handleSearch(e) {
      this.load(1, 10)
    },
    onStatusChange() {},
    load(pageNumber = 1, pageSize = 10) {
      this.loading = true
      setTimeout(() => {
        let list = []
        for (let index = 0; index < pageSize; index++) {
          let idx = (pageNumber - 1) * pageSize + index
          list.push({
            id: idx,
            realname: `用户${index}`,
            loginTime: '2021-04-21 10:' + (60 - index),
            status: index % 2,
            sex: index % 2,
            age: 30 - index,
            email: '18866669999@qq.com',
            phone: '18866669999',
            nickname: `nickname${idx}`,
            wx: `wx61265412${idx}`,
          })
        }
        let totalRow = 24
        this.pagination = {
          // firstPage,
          // lastPage,
          pageNumber: pageNumber,
          pageSize,
          // totalPage,
          totalRow,
        }
        this.list = list
        this.loading = false
      }, 500)
    },
    onPageChange(pageNumber, pageSize) {
      this.load(pageNumber, pageSize)
    },
    onPageSizeChange(pageNumber, pageSize) {
      this.load(pageNumber, pageSize)
    },
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRowKeys
      this.selectedRows = [...selectedRows]
    },
    onDelete(record) {
      console.log(record)
    },
    handleAccount(flag) {
      let vm = this
      let selectedRowKeys = vm.selectedRowKeys
      if (selectedRowKeys.length === 0) {
        this.$message.warning('请选择至少一条记录')
        return
      }
      this.$confirm({
        title: `确认${flag === 1 ? '冻结' : '解冻'}所选择的账号？`,
        cancelText: '',
        icon: 'info-circle',
        onOk() {
          let { pageNumber, pageSize } = vm.pagination
          vm.load(pageNumber, pageSize)
        },
      })
    },
  },
  mounted() {
    let { pageNumber, pageSize } = this.pagination
    this.load(pageNumber, pageSize)
  },
}
</script>
